import React from 'react';
import axios from "axios";
import Movie from "./Movie";

class App extends React.Component{
  state={
    isLoading : true,
    movies : []
  };

  getMovies = async () =>{
    const {
      data : {
        data : { movies }
      }
    } = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
    this.setState({movies, isLoading:false});
    //this.setState(movies:movies)인데 줄인 거임
    //하나는 setState의 movies 다른 하나는 axios에서 온 movies
  };

  componentDidMount(){
    // setTimeout(()=>{
    //   this.setState({isLoading:false});
    // },6000);
    //componentDidMount에서 data를 fetch, 6s간격으로
    this.getMovies();
    }
  //component가 되면 getMovies를 호출하겠다!

  render(){
    const {isLoading,movies} = this.state;
    return (
      <section class = "container">
          {isLoading ? (
          <div class = "loader">
            <span class = "loader_text"></span>
          </div>
          ) : (
            <div class="movies">
              {movies.map(movie => (
               <Movie 
               key={movie.id} 
               id={movie.id} 
               year={movie.year} 
               title={movie.title} 
               summary={movie.summary} 
               poster={movie.medium_cover_image}
               genres = {movie.genres}
               />
             ))}
            </div>
          )}
      </section>
    );
  }
}
export default App;
